"use client"

import { useState } from "react"
import styles from "./faq-section.module.css"
import { ChevronDown } from "lucide-react"

import { motion } from "framer-motion"

export function FAQSection() {
  const [openIndex, setOpenIndex] = useState(0)

  const faqs = [
    {
      question: "É realmente “grátis” o desenvolvimento?",
      answer:
        "Sim. Você paga apenas a hospedagem e manutenção conforme o plano escolhido.",
    },
    {
      question: "Posso cancelar quando quiser?",
      answer: `✅ Sim! Mas com uma condição justa:

Como o site é 100% gratuito (você economiza R$3.500), pedimos um compromisso mínimo de 6 meses para cobrir nosso investimento na criação.

Após 6 meses: Cancela quando quiser, sem multa. Aviso prévio de 30 dias.

Antes de 6 meses: Você pode cancelar, mas há uma taxa de 2 mensalidades (para cobrir parte do custo de criação que demos de graça).

Mas calma: Temos garantia de 30 dias. Se você não gostar do site nos primeiros 30 dias, devolvemos 100% e você não paga nada.`
    },
    {
      question: "Existe fidelidade mínima?",
      answer:
        "Recomendamos 12 meses para estabilidade operacional e ROI. Consulte o contrato para termos, reajustes e portabilidade.",
    },
    {
      question: "Posso migrar de plano depois?",
      answer:
        "Sim. Você pode evoluir ou reduzir conforme sua necessidade, com ajuste pró-rata.",
    },
    {
      question: "O site é meu?",
      answer:
        "Sim, o site é seu. No entanto, caso deseje receber os arquivos-fonte (códigos e assets) do projeto desenvolvido via campanha, será cobrada uma taxa de disponibilização.",
    },
    {
      question: "O que são “alterações à parte” no Essencial?",
      answer:
        "Demandas de atualização de conteúdo/ajustes são orçadas caso a caso. No Avançado há 5h/mês inclusas; no Premium, ilimitadas.",
    },
    {
      question: "Vocês cuidam de SEO e tráfego pago?",
      answer:
        "Sim, como serviços adicionais (ou inclusos via consultoria e campanhas no Premium). Podemos ativar SEO local, Google Ads, Meta Ads e conteúdo.",
    },
    {
      question: "Quais são as formas de pagamentos?",
      answer:
        `-  Cartão de crédito (assinatura recorrente): cobrança mensal automática, sem comprometer todo o limite do cartão.     
- Pagamento antecipado (6 ou 12 meses): à vista com 10% de desconto sobre o período escolhido.     
- Mensal via PIX: pagamento mês a mês, com confirmação imediata.`,
    },
    {
      question: "Vocês fazem e-commerce?",
      answer:
        "Sim, desenvolvemos e-commerce; porém este serviço não está incluído nesta campanha — solicite nossos valores para receber uma proposta com escopo e prazos.",
    },
  ]

  return (
    <section className={styles.section}>
      <motion.div
        initial={{opacity: 0,x: -50}}
        whileInView={{opacity: 1,x: 0}}
        transition={{duration: .5}}
        className={styles.container}>
        <div className={styles.header}>
          <h2 className={styles.title}>Perguntas Frequentes</h2>
          <p className={styles.subtitle}>Tire suas dúvidas sobre nossa oferta e serviços</p>
        </div>

        <div className={styles.faqList}>
          {faqs.map((faq, index) => (
            <div key={index} className={styles.faqItem}>
              <button
                className={`${styles.faqQuestion} ${openIndex === index ? styles.active : ""}`} //openIndex === index
                onClick={() => setOpenIndex(openIndex === index ? null : index)}
              >
                <span>{faq.question}</span>
                <ChevronDown size={20} className={`${styles.chevron} ${openIndex === index ? styles.rotated : ""}`} />
              </button>
              <div className={`${styles.faqAnswer} ${openIndex === index ? styles.open : ""}`}>
                <p>{faq.answer}</p>
              </div>
            </div>
          ))}
        </div>
      </motion.div>
    </section>
  )
}
