"use client"

import { Star, Quote } from "lucide-react"
import { useState, useEffect } from "react"
import styles from "./testimonials.module.css"

const Card = ({ children, className }) => <div className={`${styles.card} ${className || ""}`}>{children}</div>

export function Testimonials() {
  const testimonials = [
    {
      name: "Maria Silva",
      company: "Boutique Elegance",
      text: "A Weiblink transformou completamente nossa presença online. O site ficou lindo e nossas vendas aumentaram 300% em 6 meses!",
      rating: 5,
      image: "/placeholder.svg?height=80&width=80",
    },
    {
      name: "João Santos",
      company: "Consultoria JS",
      text: "Profissionais excepcionais! Entregaram exatamente o que prometeram, no prazo e com qualidade superior. Recomendo sem hesitar.",
      rating: 5,
      image: "/placeholder.svg?height=80&width=80",
    },
    {
      name: "Ana Costa",
      company: "Clínica Bem Estar",
      text: "O suporte da equipe é fantástico. Sempre que precisamos de alguma alteração, eles respondem rapidamente e resolvem tudo.",
      rating: 5,
      image: "/placeholder.svg?height=80&width=80",
    },
    {
      name: "Carlos Oliveira",
      company: "Tech Solutions",
      text: "Site moderno, rápido e otimizado. A hospedagem é excelente, nunca tivemos problemas de indisponibilidade.",
      rating: 5,
      image: "/placeholder.svg?height=80&width=80",
    },
  ]

  const [currentIndex, setCurrentIndex] = useState(0)

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentIndex((prevIndex) => (prevIndex + 1) % testimonials.length)
    }, 5000)
    return () => clearInterval(timer)
  }, [testimonials.length])

  return (
    <section className={styles.testimonials}>
      <div className={styles.container}>
        <div className={styles.header}>
          <h2 className={styles.title}>
            O que nossos <span className={styles.titleHighlight}>clientes</span> dizem
          </h2>
          <p className={styles.subtitle}>
            Veja os depoimentos de quem já confia na Weiblink para sua presença digital.
          </p>
        </div>

        <div className={styles.testimonialContainer}>
          <Card className={styles.testimonialCard}>
            <Quote className={styles.quoteIcon} />

            <div className={styles.testimonialContent}>
              <div className={styles.rating}>
                {[...Array(testimonials[currentIndex].rating)].map((_, i) => (
                  <Star key={i} className={styles.star} />
                ))}
              </div>

              <blockquote className={styles.testimonialText}>"{testimonials[currentIndex].text}"</blockquote>

              <div className={styles.testimonialAuthor}>
                <img
                  src={testimonials[currentIndex].image || "/placeholder.svg"}
                  alt={testimonials[currentIndex].name}
                  className={styles.authorImage}
                />
                <div>
                  <div className={styles.authorName}>{testimonials[currentIndex].name}</div>
                  <div className={styles.authorCompany}>{testimonials[currentIndex].company}</div>
                </div>
              </div>
            </div>
          </Card>

          {/* Indicators */}
          <div className={styles.indicators}>
            {testimonials.map((_, index) => (
              <div
                key={index}
                className={`${styles.indicator} ${index === currentIndex ? styles.indicatorActive : ""}`}
                onClick={() => setCurrentIndex(index)}
              ></div>
            ))}
          </div>
        </div>
      </div>
    </section>
  )
}
