import { useState, useEffect } from "react"
import { Menu, X, Code, Globe } from "lucide-react"
import {Link} from "react-router-dom"
import styles from "./header.module.css"

import Logo from "../assets/logo-png.png"

export function Header() {
  const [isMenuOpen, setIsMenuOpen] = useState(false)
  const [isScrolled, setIsScrolled] = useState(false)

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50)
    }
    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  return (
    <header className={`${styles.header} ${isScrolled ? styles.headerScrolled : styles.headerTransparent}`}>
      <div className={styles.container}>
        <div className={styles.nav}>
          <Link to="/" className={styles.logo}>
            <div className={styles.logoIcon}>
              <img src={Logo} alt="logo" />
            </div>
            <span className={styles.logoText}>WEIBLINK</span>
          </Link>

          {/* Desktop Navigation */}
          <nav className={styles.desktopNav}>
            <Link to="/#sobre" className={styles.navLink}>
              Sobre
            </Link>
            <Link to="/services" className={styles.navLink}>
              Serviços
            </Link>
            <a href="/#plans" className={styles.navLink}>
              Planos
            </a>
            <Link to="/projects" className={styles.navLink}>
              Projetos
            </Link>
            {/* <Link to="/login" className={styles.navLink}>
              Entrar
            </Link> */}
            <Link to="/new" className={styles.ctaButton}>Começar Projeto</Link>
          </nav>

          {/* Mobile Menu Button */}
          <button className={styles.mobileMenuButton} onClick={() => setIsMenuOpen(!isMenuOpen)}>
            {isMenuOpen ? (
              <X style={{ height: "24px", width: "24px" }} />
            ) : (
              <Menu style={{ height: "24px", width: "24px" }} />
            )}
          </button>
        </div>

        {/* Mobile Navigation */}
        {isMenuOpen && (
          <nav className={styles.mobileNav}>
            <div className={styles.mobileNavContent}>
              <Link to="/#sobre" className={styles.mobileNavLink}>
                Sobre
              </Link>
              <Link to="/services" className={styles.mobileNavLink}>
                Serviços
              </Link>
              <a href="/#plans" className={styles.mobileNavLink}>
                Planos
              </a>
              <Link to="/projects" className={styles.mobileNavLink}>
                Exemplos
              </Link>
              {/* <Link to="/login" className={styles.mobileNavLink}>
                Entrar
              </Link> */}
              <Link to="/new" className={styles.mobileCtaButton}>Começar Projeto</Link>
            </div>
          </nav>
        )}
      </div>
    </header>
  )
}
