"use client"

import React, { useContext } from "react"
import { useState } from "react"
import { CheckCircle2 } from "lucide-react"
import styles from "./lead-form.module.css"
import { AppContext } from "../../../AppContext"

export function LeadForm() {
  const [submitted, setSubmitted] = useState(false)
  const [loading, setLoading] = useState(false)
  const [form, setForm] = useState({
    name: "",
    mail: "",
    bussinessName: "",
    tel: "",
    website: "",
    project: "",
  })

  const { subimitFormFreeOffer } = useContext(AppContext) 

  function formatDigits(d) {
    const digits = d.replace(/\D/g, '').slice(0, 11);
    if (digits.length <= 2) return '(' + digits;
    return '(' + digits.slice(0, 2) + ') ' + digits.slice(2);
  }

  const handleSubmit = async (e) => {
    e.preventDefault()
    setLoading(true)

    // Simulate form submission
    await subimitFormFreeOffer(form);

    setLoading(false)
    setSubmitted(true)
  }

  if (submitted) {
    return (
      <div className={styles.card}>
        <div className={styles.successContent}>
          <div className={styles.successIcon}>
            <CheckCircle2 size={32} />
          </div>
          <h3 className={styles.successTitle}>Solicitação Enviada!</h3>
          <p className={styles.successMessage}>
            Obrigado pelo seu interesse! Nossa equipe entrará em contato em até 24 horas para discutir seu projeto.
          </p>
          <button onClick={() => setSubmitted(false)} className={`${styles.button} ${styles.buttonOutline}`}>
            Enviar Outra Solicitação
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className={styles.card}>
      <div className={styles.header}>
        <h2 className={styles.title}>Solicite Seu Site Grátis</h2>
        <p className={styles.description}>Preencha o formulário abaixo e receba uma proposta personalizada</p>
      </div>
      <div className={styles.content}>
        <form onSubmit={handleSubmit} className={styles.form}>
          <div className={styles.row}>
            <div className={styles.field}>
              <label htmlFor="name" className={styles.label}>
                Nome Completo *
              </label>
              <input id="name" name="name" value={form.name} onChange={(e) => setForm({...form, name: e.target.value})} placeholder="João Silva" required className={styles.input} />
            </div>
            <div className={styles.field}>
              <label htmlFor="company" className={styles.label}>
                Nome da Empresa *
              </label>
              <input id="company" name="company" value={form.bussinessName} onChange={(e) => setForm({...form, bussinessName: e.target.value})} placeholder="Minha Empresa Ltda" required className={styles.input} />
            </div>
          </div>

          <div className={styles.row}>
            <div className={styles.field}>
              <label htmlFor="email" className={styles.label}>
                E-mail *
              </label>
              <input
                id="email"
                name="email"
                type="email"
                placeholder="joao@empresa.com"
                required
                value={form.mail} 
                onChange={(e) => setForm({...form, mail: e.target.value})}
                className={styles.input}
              />
            </div>
            <div className={styles.field}>
              <label htmlFor="phone" className={styles.label}>
                Telefone/WhatsApp *
              </label>
              <input
                id="phone"
                name="phone"
                type="tel"
                placeholder="(11) 99999-9999"
                required
                value={form.tel} 
                onChange={(e) => setForm({...form, tel: formatDigits(e.target.value)})}
                className={styles.input}
              />
            </div>
          </div>

          <div className={styles.field}>
            <label htmlFor="website" className={styles.label}>
              Site Atual (se houver)
            </label>
            <input
              id="website"
              name="website"
              type="url"
              placeholder="https://meusite.com.br"
              value={form.website} 
              onChange={(e) => setForm({...form, website: e.target.value})}
              className={styles.input}
            />
          </div>

          <div className={styles.field}>
            <label htmlFor="message" className={styles.label}>
              Conte-nos sobre seu projeto *
            </label>
            <textarea
              id="message"
              name="message"
              placeholder="Descreva brevemente o que você precisa para o seu site..."
              required
              value={form.project} onChange={(e) => setForm({...form, project : e.target.value})}
              rows={4}
              className={styles.textarea}
            />
          </div>

          <button type="submit" className={styles.button} disabled={loading}>
            {loading ? "Enviando..." : "Solicitar Meu Site Grátis"}
          </button>

          <p className={styles.disclaimer}>
            Ao enviar este formulário, você concorda em receber comunicações da nossa agência.
          </p>
        </form>
      </div>
    </div>
  )
}
