import React, { createContext, useState } from 'react'

export const AppContext = createContext()

export const AppContextProvider = ({ children }) => {
    const [user, setUser] = useState();
    const [token, setToken] = useState(localStorage.getItem("tokenjwt"));
    const URL = ""
    // const URL = ""

    const subimitFormNewProject = async (formBody) => {
        console.log(formBody)
        const response = (await fetch(`${URL}/api/form/newProject`, {
            method: "POST",
            headers: {
                "Content-Type":"application/json",
            },
            body: JSON.stringify(formBody)
        }))

        localStorage.removeItem("plan")
    }

    const subimitFormFreeOffer = async (formBody) => {
        const response = await (await fetch (`${URL}/api/form/free`, {
            method: "POST",
            headers: {
                "content-type": "application/json"
            },
            body: JSON.stringify(formBody)
        })).json

        console.log(response);
        

        if(response.error) alert("erro")
    }

    return (
        <AppContext.Provider
            value={{ URL, subimitFormNewProject, user, subimitFormFreeOffer }}
        >
            {children}
        </AppContext.Provider>
    )
}

