import React, { useContext, useEffect } from 'react'
import Styles from "./FormNew.module.css"
import Form from './components/Form'
import { Link } from 'react-router-dom'
import { ArrowBigLeft, ArrowLeft } from 'lucide-react'
import { AppContext } from '../../AppContext'

import Logo from "../../assets/logo-white.png"

const FormNew = () => {
    const [subimited, setSubimited] = React.useState(false)
    const [response, setResponse] = React.useState({
        name: "",
        email: "",
        tell: "",
        type: "",
        plan: "",
    })
    const [ask, setAsk] = React.useState({
        name: "Qual seu nome?",
        email: "Qual seu email?",
        tell: "Qual seu numero de telefone?",
        type: "O Site é para uma empresa ou para uma pessoa?",
        plan: "Voce ja tem noção de qual dos nossos planos você quer? se sim qual?"
    })
    const [indexAsk, setIndexAsk] = React.useState(0)
    const askTypes = Object.entries(ask)

    const { subimitFormNewProject } = useContext(AppContext)

    useEffect(() => {
        const plan = localStorage.getItem("plan")
        if(plan) return setResponse({
            ...response,
            plan: plan
        })
    }, [])

    const nextIndex = () => {
        if (indexAsk >= askTypes.length - 1) {
            if (!response.email) return alert("O campo Email esta incompleto!")
            if (!response.tell) return alert("O campo Telefone esta incompleto!")
            if (!response.type) return alert("O campo do Tipo de site esta incompleto!")
            if (!response.plan) return alert("O campo do Tipo de Plano esta incompleto!")

            subimitFormNewProject(response).then(r => setSubimited(true));
        }
        else setIndexAsk(indexAsk + 1)
    }

    return (
        <div className={Styles.container}>
            <Link to="/" className={Styles.title}>
                <img src={Logo} alt="logo" />
            </Link>

            <div className={Styles.form}>
                {subimited ?
                    <div className={Styles.span}>Parabens Formulario entregue!
                        <br></br> um mmembro da nossa equipe entrara em contato o mais rapido possivel! <br />
                        <Link to="/">Voltar para a pagina Inicial</Link>
                    </div> : <>
                        <h2>Muito bom te ter por aqui!</h2>
                        <div className={Styles.progressContainer}>
                            {askTypes.map((o, i) => <div
                                className={`${Styles.progressIten} ${i < indexAsk && Styles.progressActive} ${i == indexAsk && Styles.progressOn}`}></div>)}
                        </div>
                        <div className={Styles.nav}>
                            <div
                                onClick={() => indexAsk > 0 && setIndexAsk(indexAsk - 1)}
                                className={Styles.navIten}>
                                <ArrowLeft size={18} /> Voltar</div>
                        </div>
                        <Form
                            ask={ask[askTypes[indexAsk][0]]}
                            response={response[askTypes[indexAsk][0]]}
                            nextIndex={() => nextIndex()}
                            isLast={askTypes.length - 1 === indexAsk}
                            askType={askTypes[indexAsk][0]}
                            setResponse={(value) => {
                                let newObj = {}
                                Object.entries(response).map((o,) => {
                                    const [ind, val] = o

                                    newObj[ind] = val;
                                    newObj[askTypes[indexAsk][0]] = value;
                                })
                                setResponse(newObj);
                            }} />
                        {/* <div className={Styles.span}>Ja é cliente? <Link to="/login">Clique aqui</Link> </div> */}
                    </>}
            </div>
            <div></div>

        </div>
    )
}

export default FormNew