import Home from "./pages/Home/Home";
import './index.css';
import { Route, Routes, useLocation } from "react-router-dom";
import ServicosPage from "./pages/services/Services";
import ExemplosPage from "./pages/exemples/exemples";
import { useEffect } from "react";
import FormNew from "./pages/NewProject/FormNew";
import LoginPage from "./pages/Login/LoginPage";
import FreeWebsite from "./pages/offer/FreeWebsite";
import FreeForm from "./pages/offer/form/FreeForm";

function App() {
  const { pathname } = useLocation()

  useEffect(() => {
    window.scrollTo(0, 0)
  }, [pathname])


  return (
    <div className="App">
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/services" element={<ServicosPage />} />
        <Route path="/projects" element={<ExemplosPage />} />
        <Route path="/new" element={<FormNew />} />
        <Route path="/login" element={<LoginPage />} />
        <Route path="/offer" element={<FreeWebsite />} />
        <Route path="/offer/form" element={<FreeForm />} />
      </Routes>
    </div>
  );
}

export default App;
