import React from 'react'
import Styles from "./Form.module.css"

const Form = ({ ask, response, setResponse, askType, isLast, nextIndex }) => {

    function formatDigits(d) {
        const digits = d.replace(/\D/g, '').slice(0, 11);
        if (digits.length <= 2) return '(' + digits;
        return '(' + digits.slice(0, 2) + ') ' + digits.slice(2);
    }

    return (
        <form
            className={Styles.form}
            key={ask}

            onSubmit={(e) => {
                e.preventDefault()
                nextIndex()



            }}>
            <label className={Styles.ask} htmlFor={askType}>{ask}</label>

            {askType == "type" ? <div className={Styles.radio}>
                <div>
                    <input
                        value={response}
                        checked={response === "Pessoal"}
                        required
                        onChange={(i) => {
                            setResponse("Pessoal")
                        }}
                        type="radio" name="teste" placeholder='teste' id="person" />
                    <label htmlFor="person">Pessoa</label>
                </div>
                <div>
                    <input
                        checked={response === "Empresarial"}
                        onChange={(i) => {
                            setResponse("Empresarial")
                        }}
                        required

                        type="radio" name="teste" placeholder='teste' id="empresa" />
                    <label htmlFor="empresa">Empresa</label>
                </div>
            </div> : <input
                className={Styles.input}
                id={askType}
                required
                type={askType == "email" && "email" || askType == "tell" && "tel" || "text"}
                value={response}
                onChange={(i) => {
                    if (askType === "tell") return setResponse(formatDigits(i.target.value))
                    setResponse(i.target.value)
                }} />}

            <button className={Styles.btn}>{isLast ? "Enviar" : "Continuar"}</button>
        </form>

    )
}

export default Form